const setParentHelper = (parent, children) => {
  children.forEach(c => {
    if (!c.parent.find(arr => arr.id === parent.id)) {
      c.parent.push(parent)
    }
    setParentHelper(c, c.children)
  });
};
heroLvs[heroLvs.length - 1].forEach(hero => {
  setParentHelper(hero, hero.children)
})

